<template>
  <div class="error-page">
    <div class="error-code">4<span>0</span>4</div>
    <div class="error-desc">啊哦~ 你所访问的页面不存在</div>
    <div class="error-handle">
      <span class="error-btn" @click="goHome">返回首页</span>
      <span class="error-btn" @click="goBack">返回上一页</span>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'error404',
  computed: {
    ...mapGetters(['routes'])
  },
  methods: {
    goHome() {
      const visitRoutes = this.routes.filter(item => !item.hidden)
      this.$router.push({ path: visitRoutes[0].path, replace: true })
    },
    goBack() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
.error-page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: #f3f3f3;
  box-sizing: border-box;
  .error-code {
    line-height: 1;
    font-size: 250px;
    font-weight: bolder;
    color: #2d8cf0;
    .error-code span {
      color: #00a854;
    }
  }
  .error-desc {
    font-size: 30px;
    color: #777;
  }
  .error-handle {
    margin-top: 30px;
    padding-bottom: 200px;
  }
  .error-btn {
    color: #2a4eff;
    cursor: pointer;
    &:first-child {
      margin-right: 30px;
    }
  }
}
</style>
